___CSS_LOADER_EXPORT___.push([module.id, `* {
  box-sizing: border-box;
}

.flex {
  display: flex;
  align-items: center;
  justify-content: center;
}

body {
  font-family: 'Acme','Pacifico', cursive, monospace, sans-serif, serif;
  color: rgb(100, 100, 100);
  overflow: hidden;
  margin: 0;
  min-height: 100vh;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-repeat: no-repeat;
  background-position: center;

}

.container {
  padding-top: 230px;
  flex-direction: column;
  max-width: 1200px;
  margin: auto;
}

.board {
  align-items: flex-start;
  gap: 100px;
  padding: 5px;
  margin: 5px;
  width: 90%;
}

h2 {
  font-size: 36px;
}

.showscore {
  width: 40%;
}

.scorehead {
  justify-content: space-between;
}

.scorelist ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.scorelist ul li {
  border: 1px solid gray;
  border-radius: 5px;
  height: 25px;
  margin: 7px 0;
  padding: 0 3px;
}

.shade-m {
  background-color: rgb(85, 85, 85);
  color: white;
}

.shade {
  background-color: rgb(151, 150, 150);
  color: white;
}

.scorelist span {
  display: inline-block;
  height: 100%;
}

.pname {
  width: 80%;
}

.addscore {
  width: 40%;
}

.form {
  flex-direction: column;
}

.form input {
  width: 96%;
  height: 25px;
  margin: 5px;
  border-style: none;
  border-radius: 3px;
  font-family: 'Acme','Pacifico', cursive, monospace, sans-serif, serif;
}

.form button {
  height: 25px;
  margin: 6px 7px 5px 78%;
  border: 2px solid rgb(1, 204, 255);
  border-radius: 5px;
  color: rgb(75, 75, 75);
  background-color: rgb(1, 204, 255);
  font-family: 'Acme','Pacifico', cursive, monospace, sans-serif, serif;
}

.refresh {
  height: 25px;
  border: 2px solid rgb(1, 204, 255);
  border-radius: 5px;
  color: rgb(75, 75, 75);
  background-color: rgb(1, 204, 255);
  font-family: 'Acme','Pacifico', cursive, monospace, sans-serif, serif;
}
